:root {
  --background: #300a24;
  --white: #ffffff;
  --highlight-green:#86e033;
  --highlight-blue:#76A1D4;
  --highlight-orange:#feb449;
  --highlight-yello:#00928B;
}

body {
  background: #300a24;
  color: rgb(255, 255, 255);
  font-family: monospace;
  height: 100vh;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: scroll;
  word-break: break-all;
  margin: 0;
  padding: 16px;
}

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.greet{
  color: var(--highlight-blue);
  font-size: 00.9rem;
}

.greet a{
  color: var(--highlight-blue);
  font-size: 00.9rem;
}

.example + span{
  color: var(--highlight-green);
  font-size: 00.9rem;
}

.line {
  cursor: text;
}

.line-head {
  color: var(--highlight-green);
  font-size: 0.9rem;
  initial-letter: 0px;
  line-height: 100%;
  outline: none;
  visibility: visible;
  display: inline;
}

.line-head > span{
  color: var(--highlight-blue);
}

.line-command {
  font-size: 0.9rem;
  initial-letter: 0px;
  line-height: 100%;
  outline: none;
  visibility: visible;
  display: inline;
}

.line-tail {
  width: 100%;
  font-size: 0.8rem !important;
  color: var(--highlight-blue);
  text-align: end;
  display: inline-block;
  cursor: pointer;
}

.line-tail > svg {
  margin-left: 10px;
}
.line-tail > span {
  color: var(--highlight-yello);
  margin-left: 10px;
  font-size: 0.9rem !important;
  font-weight: 800;
}

.line-command:focus {
  /*background-color: #343A40;
  outline-style: solid;
  outline-color: #343A40;
  outline-width: 3px;*/
  color: var(--highlight-orange);
}

.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
